.header {
  width: 100%;
  transition: background-color var(--bs-transition);
  z-index: 50;
  margin-bottom: 20px;

  .header-is-sticky & {
    // ..стили для липкого хедера
    position: fixed;
    background-color: var(--white-color);
  }

  // .header__container
  & .container {
    height: 100%;

    .header-is-sticky & {
      height: 100%;
    }
  }

  & .row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }

  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;

    & > :not(:last-child) {
      margin-right: 20px;
    }
  }

  .main-navigation {
    margin: 0 auto;
  }

  @import "main-navigation";
}

