.footer {
  color: var(--white-color);
  padding: 40px 0;
  background: var(--black-color);

  a {
    color: var(--white-color);
  }

  hr {
    border-top-color: var(--white-color);
  }

  // .footer__container
  &__container {
    display: block;

    @include media-mx(510) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__logo {

  }

  &__navigation {
    display: flex;
    align-items: flex-start;

    .col {
      text-align: center;
    }

    &-heading {
      margin-bottom: 10px;
    }

    .menu {
      &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
      }

      &__item {
        &:not(:last-child) {
          margin-bottom: 5px;
        }
      }

      &__link {
        display: block;
        color: var(--white-color);
        padding: 5px 10px;
      }
    }
  }

  .social-media-list {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    &__item {
      &:not(:last-child) {
        margin-right: 8px;
      }
    }

    &__link {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      height: 48px;
      width: 48px;
    }
  }

  .newsletter-form {
    &__heading {
      font-weight: 700;
      font-size: 12px;
      line-height: 16px;
      text-transform: uppercase;
    }
    .input-group {
      margin: 5px 0;
      input[type=email] {
        color: darkgray;
        font-size: 18px;
        line-height: 24px;
        height: 50px;
        padding: 5px;
        border-radius: 1px;
      }
    }
    &__subtext {
      font-size: 12px;
      line-height: 16px;
    }
  }
}
