.news-sidebar {
  padding-right: 8px;
  border-right: 1px solid var(--vr-color);

  &__heading {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  &__divider {
    font-size: rem(12);
    font-weight: 700;
    line-height: rem(16);
    letter-spacing: 0.02em;
    text-align: left;
    padding: 16px 0;
  }

  .article {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;

    padding: 16px 0;
    border-bottom: 1px solid var(--gray-color-secondary);

    & time {
      color: var(--gray-color-primary);
      font-size: 1rem;
      font-weight: 700;
      text-align: left;
      margin-right: 16px;
    }

    &__wrapper {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
    }

    &__header {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      width: 100%;
      margin-bottom: 4px;

      font-size: rem(12);
      line-height: rem(16);
      text-transform: uppercase;

      & > *:not(:last-child) {
        margin-right: 10px;
      }
    }

    &__tag {
      padding: 0 2px;
      border-radius: 1px;
      color: var(--white-color);
      background-color: var(--gray-color-primary);
    }

    &__category {
      color: var(--gray-color-primary);
    }

    &__title {
      font-size: rem(16);
      font-weight: 400;
      line-height: rem(24);
      text-align: left;
    }

    &--important {
      & .article__tag {
        background-color: var(--primary-color);
      }

      & .article__title {
        font-weight: 700;
      }
    }

    &--promo {
      & .article__tag {
        background-color: var(--green-color);
      }

      & .article__title {
        color: var(--green-color);
        font-weight: 700;
      }
    }
  }

  &__cta {
    display: block;
    margin: 20px auto 0 auto;
  }
}