.article-cards {

  &--grid {
    --grid-layout-gap: 10px;
    --grid-column-count: 6;
    --grid-item--min-width: 200px;

    --gap-count: calc(var(--grid-column-count) - 1);
    --total-gap-width: calc(var(--gap-count) * var(--grid-layout-gap));
    --grid-item--max-width: calc((100% - var(--total-gap-width)) / var(--grid-column-count));

    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(max(var(--grid-item--min-width), var(--grid-item--max-width)), 1fr));
    grid-gap: var(--grid-layout-gap);
  }

  &--grid-col-2 {
    --grid-column-count: 2;
  }

  &--grid-col-3 {
    --grid-column-count: 3;
  }

  &--grid-col-4 {
    --grid-column-count: 4;
  }

  &--large {
    & .article-card {
      &__bottom {
        flex-direction: row !important;
        justify-content: space-between;
      }
    }
  }

  .article-card {
    display: flex;
    flex-direction: column;

    &__image {
      margin-bottom: 16px;
    }

    &__header {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-bottom: 12px;

      font-size: rem(12);
      font-weight: 500;
      line-height: rem(16);
      text-transform: uppercase;

      & > *:not(:last-child) {
        margin-right: 10px;
      }
    }

    &__tag {
      padding: 0 2px;
      border-radius: 1px;
      color: var(--white-color);
      background-color: var(--gray-color-primary);
    }

    &__category {
      color: var(--gray-color-primary);
    }

    &__title {
      font-size: rem(18);
      font-weight: 500;
      line-height: rem(24);
    }

    &__bottom {
      display: flex;
      flex-direction: column;
      color: var(--gray-color-primary);
      font-size: rem(12);
      font-weight: 700;
      line-height: rem(16);
      margin-top: 12px;
    }

    &--promo {
      & .article-card__tag {
        background-color: var(--green-color);
      }

      & .article-card__title {
        color: var(--green-color);
        font-weight: 700;
      }
    }
  }

}