.language-switcher {
  display: flex;
  align-items: center;
  justify-content: center;

  &__option {
    display: block;
    padding: 5px;
    cursor: pointer;
    margin: 0 5px;
    position: relative;

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 5px;
      width: calc(100% - 10px);
      height: 2px;
      border-radius: 0 0 1px 1px;
      background-color: var(--black-color);
      opacity: 0;
    }

    &:hover::after,
    &:focus::after,
    &--active::after{
      opacity: 1;
      transform: translate3d(0, 0.2em, 0);
    }
  }
}