.pagination {
  display: block;
  margin: 20px 0;

  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__item {

    &--disabled {
      color: var(--gray-color-primary) !important;

      & .pagination__link {
        color: var(--gray-color-primary) !important;
      }
    }

    &--active {
      & .pagination__link {
        border-color: var(--black-color);
      }
    }

    &:hover:not(.active),
    &:active {
      & .pagination__link {
        border-color: var(--black-color);
      }
    }
  }

  &__link {
    color: var(--black-color);
    padding: 8px 16px;
    border: 2px solid transparent;
  }
}