.main-navigation {

  .menu__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;

    & .menu__item {

      &:not(:last-child) {
        margin-right: 15px;
      }

      &--active {
        & .menu__link {
          &::after {
            opacity: 1;
            transform: translate3d(0, 0.2em, 0);
          }
        }
      }

      &:hover {

      }
    }

    & .menu__link {
      display: block;
      padding: 5px 10px;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 10px;
        width: calc(100% - 20px);
        height: 2px;
        border-radius: 0 0 1px 1px;
        background-color: var(--primary-color);
        opacity: 0;
      }

      &:hover::after {
        opacity: 1;
        transform: translate3d(0, 0.2em, 0);
      }
    }
  }
}