.search-bar {
  display: flex;
  align-items: center;
  justify-content: left;
  height: 50px;

  &__button {
    height: 48px;
    width: 48px;
    background: no-repeat center / 100% url("/images/icons/search.svg");
  }

  &__input {
    height: 100%;
    border: 1px solid var(--black-color);
    border-radius: 5px;
    padding: 5px 10px;
    transform: translateX(-100%);
    opacity: 0;
    transition: opacity .25s, transform .25s;
  }

  &--active {
    input[type=search] {
      transform: translateX(0);
      opacity: 1;
    }
  }
}