.banner {
  height: 224px;
  background: no-repeat center right / 35% 100% url('/images/banner-background-1.svg'), var(--banner-background-color);
  padding: 32px 56px;
  position: relative;

  &__heading {
    color: var(--black-color);
    font-size: rem(32);
    font-weight: 500;
    line-height: rem(40);
    text-align: left;
    padding-right: 35%;
  }

  &__logo {
    position: absolute;
    left: 56px;
    bottom: 32px;
  }

  &__cta {
    position: absolute;
    bottom: 50%;
    right: 7%;
    transform: translate(7%, 50%);
  }
}