.button {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  padding: 12px 24px;
  transition: var(--bs-transition);

  &--primary {
    background-color: var(--primary-background-color);
    border: 2px solid transparent;

    &:hover {
      background-color: var(--primary-hover-color);
      border-color: #001412;
    }

    &:active {
      color: var(--white-color);
      background-color: var(--black-color);
      border-color: #001412;
    }

    &:disabled,
    &[disabled] {
      background-color: var(--primary-disabled-background-color);
    }
  }

  &--secondary {
    background-color: var(--secondary-background-color);
    border: 2px solid var(--black-color);

    &:hover {
      color: var(--primary-color);
      border-color: var(--primary-color);
      background-color: var(--secondary-hover-background-color);
    }

    &:active {
      color: var(--primary-color);
      background-color: var(--secondary-active-background-color);
    }

    &:disabled,
    &[disabled] {
      color: var(--secondary-disabled-background-color);
    }
  }
}

a {
  color: var(--black-color);

  &.text-underline {

  }

  &.link {
    display: inline-block;
    padding: 5px 10px;
    position: relative;

    &--underline {
      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 10px;
        width: calc(100% - 20px);
        height: 2px;
        border-radius: 0 0 1px 1px;
        background-color: var(--primary-color);
        opacity: 1;
        transform: translate3d(0, 0.2em, 0);
      }
    }
  }

  &:hover {
    color: var(--primary-color);
  }
}

