$mainOffset: 15; // отступы безопасности для контейнера
$maxWidth: 1920; // максимальная ширина страницы
$minWidth: 320; // минимальная ширина телефона
$containerWidth: 1280; // ширина контейнера из макета
$maxWidthContainer: $containerWidth + ($mainOffset * 2); // ширина контейнера с учетом padding слева и справа

$headerHeight: 100;
$footerHeight: 100;

:root {
  --bs-font-family: 'Stolzl', sans-serif; // Семейство шрифта по умолчанию
  --bs-font-size: 16px; // Размер шрифта по умолчанию
  --bs-font-weight: 400; // Жирность шрифта по умолчанию
  --bs-line-height: 24px; // Базовая высота шрифта
  --bs-font-color: #222; // Цвет шрифта по умолчанию
  --bs-transition: .3s ease-in-out;
  --bs-radius: 10px;

  --primary-color: #F3903F;
  --primary-hover-color: #F3903F;
  --secondary-color: #fff;
  --secondary-hover-color: #000;

  --white-color: #fff;
  --gray-color-primary: #9B9B9B;
  --gray-color-secondary: #A8A8A8;
  --black-color: #001412;
  --green-color: #008F7E;

  --primary-background-color: #F3903F;
  --primary-hover-background-color: #F3903F;
  --primary-active-background-color: #F3903F;
  --primary-disabled-background-color: #9B9B9B;

  --secondary-background-color: #fff;
  --secondary-hover-background-color: #fff;
  --secondary-active-background-color: #F3903F1A;
  --secondary-disabled-background-color: #9B9B9B;

  --banner-background-color: #FAF9F0;

  --bs-shadow: 0px 2px 3px rgba(#000, 10%), 0px 8px 3px rgba(#000, 10%);
  --hover-shadow: 0 0 5px 0 rgba(#000, 10%),
                  0 0 10px 0 rgba(#000, 10%),
                  0 0 15px 0 rgba(#000, 10%);

  --hr-color: #001412;
  --vr-color: #A8A8A8;
}